@import url(./font.css);

body {
  background-color: black;
  width: 100%;
  height: 100%;
  font-family: Monzerrat-Regular;
  margin: 0;
  padding: 0;
}

:root {
  --border-radius: 10px;
}

.content.contact-section.active-section {
  margin-top: -20px;
}

.content.contact-section.active-section .sub-title {
  color: cyan;
}

.content.contact-section.active-section .title {
  font-size: 30px;
}

.contact-container {
  display: flex;
  justify-content: space-around;
  position: relative;
  width: 40vw;
  max-height: 360px;
  opacity: 0.85;
  font-family: Monzerrat-Regular;
  border-radius: 10px;
  border: 1px solid rgba(255, 255, 255, 0.07);
  box-shadow: 4px 7px 15px 0px rgba(0, 0, 0, 0.08), 5px -5px 12px 0px rgba(255, 255, 255, 0.05) inset, -5px 5px 12px 0px rgba(255, 255, 255, 0.05) inset, -1px 0px 2px 0px rgba(201, 201, 201, 0.10) inset;
}

.contact-preview {
  height: inherit;
  border-radius: 0 var(--border-radius) var(--border-radius) 0;
}

.contact-preview img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: 0 var(--border-radius) var(--border-radius) 0;
}
.contact-blur{
  position: absolute;
  border-radius: 10px;
border: 1px solid rgb(206, 199, 199);
box-shadow: inset 0 0 2000px 1000px rgb(32, 32, 32);
filter: blur(2px);
  z-index: -1;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}

.contact-content {
  color: white;
  height: 100%;
  width: calc(100% / 2);
  border-radius: var(--border-radius) 0 0 var(--border-radius);
}

.contact-us-form {
  width: 90%;
  height: 90%;
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-start;
  align-items: center;
  padding: 10px;
  margin: 0;
  margin-left: 10px;
}

.contact-us-form h2 {
  margin-bottom: 25px;
  font-size: 15px;
  text-align: left;
}

.section {
  margin-bottom: 10px;
  margin-right: 30px;
  height: 20px;
  width: calc(100% / 3);
  display: flex;
  flex-direction: column;
}

label {
  font-size: 8px;
  color: #8D8D8D;
  text-align: left;
  margin-bottom: -15px;
  transition: font-size 0.3s ease;
}

.contact-us-form input {
  padding: 5px 0;
  background-color: unset;
  border: none;
  border-bottom: 1px solid #FFF;
  filter: brightness(2);
  font-size: 10px; /* Cambia el tama침o del texto cuando est치 seleccionado */
  transition: border-color 0.3s ease, filter 0.3s ease;
}

.contact-us-form input:focus {
  border-color: cyan;
  outline: none;
  font-size: 10px; /* Cambia el tama침o del texto cuando est치 seleccionado */
}

.contact-us-form button {
  display: block;
  margin-top: 10px;
  margin-right: 250px;
  padding: 4px 15px;
  font-size: 9px;
  background-color: unset;
  color: white;
  border: none;
  border-radius: 35px;
  border: 1px solid #FFF;
  filter: brightness(3);
  cursor: pointer;
}

.footer-form {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  margin-top: 20px;
}

.footer-form p {
  font-size: 9px;
  text-align: left;
  margin: 0;
}

.footer-form a {
  color: white;
  transition: color 0.3s ease;
}

.footer-form a:hover {
  color: cyan;
}

.follow-us {
  margin-top: 25px;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  width: inherit;
  font-size: 15px;
  filter: invert();
}

.address,
.contact,
.follow-us {
  flex: 1;
  margin-right: 20px;
}

.follow-us p {
  margin-right: 10px;
  color: #8D8D8D;
}

.follow-us .icon {
  margin-right: 3px;
  padding: 0;
  width: 18px;
  height: 18px;
  background-color: white;
  filter: invert();
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.follow-us .icon img {
  width: 55%;
  height: 55%;
}
